/* Notifications  */

.successNote {
  background-color: #42855b !important;
  border: 1px solid #3d495b !important;
  color: white !important;
  text-align: center;
  position: fixed;
  top: 0px;
  width: 100%;
  height: 50px;
  border-radius: 5px;
  padding: 10px;
  margin-left: 0px !important;
}

.failureNote {
  background-color: red !important;
  border: 1px solid #3d495b !important;
  color: white !important;
